import { Group } from '@visx/group';
import { Circle } from '@visx/shape';
import { scaleOrdinal } from '@visx/scale';

/**
 * @param data Object
 * @param total number
 * @param scale ScaleOrdinal
 * @param width number
 * @param height number
 * @param gridHeight number
 * @param gridWidth number
 */

const dotPadding = 3;
const margin = {top: 20, bottom: 20, left: 20, right: 0};

const DotMatrix = ({data, total, colorScale, activeCategories, gridHeight, gridWidth, width, height, accessor, categoryAccessor}) => {
  // map data to <Circle /> components

  const xMax = width - margin.left - margin.right;
  const yMax = height - margin.top - margin.bottom;

  // functions to position dots in the grid
  const dotCenterX = (i, max) => radius + ((i % gridWidth) * (max/gridWidth));
  const dotCenterY = (i, max) => radius + (Math.floor(i / gridWidth) * (max/gridHeight));
  const radius = xMax < yMax ? (xMax / gridWidth / 2) - dotPadding : (yMax / gridHeight / 2) - dotPadding;

  const compose = (scale, accessor) => data => scale(accessor(data));
  const dotColor = compose(colorScale, d => d.category);
  // below commented until progress is re-integrated
  // const getDotColor = (d, i, progress) => (Math.ceil(progress * dotArray.length) > i) ? dotColor(d) : '#ffffff';
  // const currentCategory = (progress) => {
  //   const currentIndex = Math.round(progress * dotArray.length);
  //   return (currentIndex < dotArray.length) ? dotArray[currentIndex].category : dotArray[dotArray.length - 1].category;
  // }

  const getDotColor = (d) => (activeCategories.indexOf(d.category) > -1) ? dotColor(d) : '#eee';

  const getDotCount = (number, total) => Math.round((number / total) * gridWidth * gridHeight);
  const dotCounts = data.map((d) => {
    return {
      dotCount: getDotCount(d[accessor], total),
      category: d[categoryAccessor],
    }
  });

  const dotArray = dotCounts.reduce((acc, curr) => {
    const arr = Array(curr.dotCount);
    arr.fill({category: curr.category});
    return acc.concat(arr);
  }, []);

  const dots = dotArray.map((d, i) => {
    return (
      <Group key={`dot${i}`}>
        <Circle r={radius} cx={dotCenterX(i, xMax)} cy={dotCenterY(i, yMax)} fill={getDotColor(d)} />
      </Group>
    );
  })

  return (
    <>
    <svg width={width} height={height}>
      {dots}
    </svg>
    </>
  );
}

export default DotMatrix;