const CustomTick = (props, child) => {
  return props.props.ticks.map((tick, i) => {
    const valueSplit = tick.formattedValue.split("A city of");
    return <text
      x={tick.to.x}
      y={tick.to.y -props.props.tickLabelProps[i].fontSize }
      textAnchor={props.props.tickLabelProps[i].textAnchor}
    >
      <tspan 
        fontWeight="bold" 
        x={tick.to.x} 
        dy={`${props.props.tickLabelProps[i].fontSize * 1.2}px`}
      >
        {valueSplit[0]}
      </tspan>
      <tspan 
        x={tick.to.x} 
        dy={`${props.props.tickLabelProps[i].fontSize * 1.2}px`}
        fillOpacity={0.5}
      >
        {`A city of ${valueSplit[1]}`}
      </tspan>
    </text>})
}

export default CustomTick;