import { scaleOrdinal } from '@visx/scale';
import { schemeSet2 } from 'd3-scale-chromatic';
import { LegendOrdinal, LegendItem, LegendLabel } from '@visx/legend';
import ParentSize from '@visx/responsive/lib/components/ParentSize';
import DotMatrix from './DotMatrix';
import { useState, useEffect, useMemo } from 'react';

var dataset = [
  {group: 'Female', category: 'White', count: 0.0},
  {group: 'Female', category: 'Asian', count: 0.0},
  {group: 'Female', category: 'Hispanic', count: 16.5},
  {group: 'Female', category: 'Black', count: 29.5},
  {group: 'Male', category: 'White', count: 4.2},
  {group: 'Male', category: 'Asian', count: 7.7},
  {group: 'Male', category: 'Hispanic', count: 95.5},
  {group: 'Male', category: 'Black', count: 371.5},
];

const uniqueCategories = dataset.reduce((acc, curr) => {
  if(acc.indexOf(curr.category) === -1){
    acc.push(curr.category);
  }
  return acc;
}, []);

const uniqueGroups = dataset.reduce((acc, curr) => {
  if(acc.indexOf(curr.group) === -1){
    acc.push(curr.group);
  }
  return acc;
}, []);

const totalCount = dataset.reduce((acc, curr) => {
  acc += curr.count;
  return acc;
}, 0);

const groupedData = uniqueGroups.map((group) => {
  return {
    groupName: group,
    data: dataset.filter((datum) => datum.group === group)
  }
});

/**
 * Layout stuff
 */
const margin = {top: 20, bottom: 20, left: 0, right: 0};

/**
 * For a legend that runs alongside the chart
 * Currently, it is paced underneath
 */
const colorswatchWidth = 10;
const labelColumnWidth = 16;
const legendGutter = 5;
const legendPadding = 16;
const labelGutter = 4;


/**
 * This is being used for the legend. Could be replaced.
 */
const x = d => d.category;

const colorScale = scaleOrdinal({
  range: ['#cea9be', '#e7466d', '#dde44c', '#ff7c53'],
  domain: dataset.map(x), // alternatively uniqueGroups
});

const gridHeight = 18; // in dots
const gridWidth = 14;
  
// sum all counts and determine percentage share for each category
// const getTotal = (data) => data.reduce((acc, curr) => acc + curr.frequency ,0);
// const total = getTotal(data1); // tally counts in data
// const getDotCount = (number, total) => Math.round((number / total) * gridWidth * gridHeight);
// const dotCounts = data1.map((d) => {
//   return {
//     dotCount: getDotCount(d.frequency, total),
//     category: d.letter,
//   }
// });

const ExampleDotMatrix = function({progress, width, height}) {
  /**
   * Layout stuff
   */
  const xMax = width  - margin.left - margin.right;
  const availableWidth = xMax - colorswatchWidth - labelColumnWidth - legendGutter - legendPadding - labelGutter;

  /**
   * Currently used for legend highlighting. Another approach would be better.
   * Probably a good use case for state management.
   */
  // const dotArray = dotCounts.reduce((acc, curr) => {
  //   const arr = Array(curr.dotCount);
  //   arr.fill({category: curr.category});
  //   return acc.concat(arr);
  // }, []);

  // const currentCategory = (progress) => {
  //   const currentIndex = Math.round(progress * dotArray.length);
  //   return (currentIndex < dotArray.length) ? dotArray[currentIndex].category : dotArray[dotArray.length - 1].category;
  // }

  // const [activeCategories, setActiveCategories] = useState([]);

  // useEffect(() => {
  //   const activeCategoryIndex = Math.floor(progress * uniqueCategories.length);
  //   if (activeCategoryIndex === activeCategories.length - 1) {
  //     return;
  //   }
  //   const newActiveCategories = uniqueCategories.slice(0, activeCategoryIndex + 1);
  //   // console.log(newActiveCategories);
  //   setActiveCategories(newActiveCategories);
  // }, [progress]);

  const activeCategories = useMemo(() => {
    const activeCategoryIndex = Math.floor(progress * uniqueCategories.length);
    return progress > 0 ? uniqueCategories.slice(0, activeCategoryIndex + 1) : [];
  }, [progress])

  const dotMatrices = groupedData.map((group) => {
    const keyName = group.groupName.toLowerCase().split(' ').join('-');
    return <div key={`dot-matrix-container-${keyName}`} className='basis-1/2' style={{height: (availableWidth/groupedData.length)}}>
      <h4>{group.groupName}</h4>
      <ParentSize>
       {
        ({width, height}) => <DotMatrix 
          data={group.data}
          total={totalCount}
          width={width}
          height={height}
          gridWidth={gridWidth}
          gridHeight={gridHeight}
          colorScale={colorScale}
          activeCategories={activeCategories}
          accessor="count"
          categoryAccessor="category"
        />
       }
      </ParentSize>
    </div>
  });
  return (
    <>
      <div className="flex basis-3/4 items-start">
        {dotMatrices}
      </div>
      <LegendOrdinal direction="column" scale={colorScale}  labelFormat={(label) => `${label}`}>
        {(labels) => (
          <div style={{ fontSize: `16px`, paddingLeft: `${legendPadding}px` }} className="basis-1/4 pr-16">
            {labels.map((label, i) => (
                  <div
                    className='p-2 my-2 text-center'
                    style={{
                      backgroundColor: (activeCategories.indexOf(label.text) > -1) ? label.value : '#fff', 
                      boxSizing: 'border-box', 
                      border: `2px solid ${label.value}`,
                      transition: 'background-color .5s'
                    }}
                  >
                    {label.text.toUpperCase()}
                  </div>

            ))}
          </div>
        )}
      </LegendOrdinal>
    </>
  );
}

export default ExampleDotMatrix;