import { voronoi, VoronoiPolygon } from '@visx/voronoi';
import { Group } from '@visx/group';
import { getSeededRandom } from '@visx/mock-data';
import { useMemo } from 'react';

const seededRandom = getSeededRandom(0.88);

const maxStrokeWidth = 60;

const defaultMargin = {
  top: 0,
  left: 0,
  right: 0,
  bottom:0,
};

const cellCount = 100;

const data = new Array(cellCount).fill(null).map(() => {
  return {
    x: seededRandom(),
    y: seededRandom(),
  }
});

const words = ['one', 'two', 'three', 'four', 'five', 'six', 'seven', 'eight', 'nine', 'ten', 'eleven', 'twelve', 'thirteen', 'fourteen', 'fifteen', 'sixteen'];

const wordModulo = Math.floor(cellCount / words.length);

const colors = ['#ff7c53', '#dde44c', '#e7466d', '#cea9be', '#217ebe', '#cdcfde'];

const getFill = (index) => colors[(index % colors.length)];

const VoronoiDiagram = ({width, height, progress, margin = defaultMargin}) => {
  const innerWidth = width - margin.left - margin.right;
  const innerHeight = height - margin.top - margin.bottom;

  const dataSlice = data.slice(0, Math.round(progress * data.length));

  const voronoiLayout = useMemo(
    () =>
      voronoi({
        x: (d) => d.x * innerWidth,
        y: (d) => d.y * innerHeight,
        width: innerWidth,
        height: innerHeight,
      })(dataSlice),
    [innerWidth, innerHeight, dataSlice],
  );

  const polygons = voronoiLayout.polygons();

  return (
    <div style={{width: `${width}px`, height: `${height}px`, position: 'relative'}}>
      <svg width={width} height={height}>
        <Group
          top={margin.top}
          left={margin.left}
        >
          <rect width={width} height={height} fill='white' stroke="black" strokeWidth={maxStrokeWidth - (progress * maxStrokeWidth)} />
          {polygons.map((polygon, i) => (
            <VoronoiPolygon
              key={`polygon-${i}`}
              polygon={polygon}
              stroke="black"
              strokeWidth={maxStrokeWidth - (progress * maxStrokeWidth)}
              fill={getFill(i)}
            />
          ))}
          {/*{
            dataSlice.map((point, i) => {
              return (i % wordModulo === 0) ? <text x={point.x * innerWidth} y={point.y * innerHeight} fontSize={16}>{words[Math.floor(i / (cellCount / words.length))]}</text> : <text x={point.x} y={point.y} fontSize={16}>hi</text>;
            })
          }*/}
        </Group>
      </svg>
      {
        dataSlice.map((point, i) => {
          return ((i > 0) && (i % wordModulo === 0)) ? <div style={{position: 'absolute', top: `${point.y * innerHeight}px`, left: `${point.x * innerWidth}px`, background: '#fff', padding: '.25rem'}}>{words[Math.floor(i / (cellCount / words.length))]}</div> : null;
        })
      }
    </div>
  );
}

export default VoronoiDiagram;